
import { Component, Prop, Vue } from "vue-property-decorator";
import LsHeader from "@/components/layout/header.vue";
import Layout from "@/layout/index.vue";
import LsAside from "@/components/layout/aside.vue";
import { asyncRoutes } from "@/router";
@Component({
    components: {
        LsHeader,
        Layout,
        LsAside,
    },
})
export default class Aside extends Vue {
    get asideMenu() {
        const { meta } = this.$route;
        let item = meta?.moduleName ? asyncRoutes.find((item) => item.meta?.moduleName === meta?.moduleName) : asyncRoutes.find((item) => item.path === meta?.parentPath);
        return item?.children
    }
    get hideAsideMenu() {
        return this.asideMenu?.every((item: any) => item.meta?.hidden);
    }
}
